#about{
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    color: var(--white);

    padding: 0 8rem;
}

.about-separator{
    /* margin: 1rem 0; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.about-separator:first-of-type{
    margin-bottom: 3rem;
}
.about-separator img{
    max-width: 30%;
    margin-left: 2rem;
}

.about-separator p{
    font-size: 1.4rem;
}

@media screen and (max-width: 600px) {
    #about{
        padding: 0 1rem;
    }
    .about-separator{
        flex-direction: column;
    }
}

@media screen and (min-width: 1600px) {
    .about-separator p{
        font-size: 1.8rem;
    }
}